import React from 'react';
import { Zoom } from 'react-slideshow-image';
import './LandingPage.scss';

import app1 from '../../images/landing/a.jpg';
import app2 from '../../images/landing/ap.jpg';
import app3 from '../../images/landing/Apostle_1.jpg';
import app4 from '../../images/landing/Apostle_2.jpg';
import app5 from '../../images/landing/app.jpg';
import app6 from '../../images/landing/apostle.jpg';
import app7 from '../../images/landing/2.png';
import app8 from '../../images/landing/3.jpg';
import app9 from '../../images/landing/4.jpg';
import app10 from '../../images/landing/5.jpg';
import app11 from '../../images/landing/6.jpg';
import app12 from '../../images/landing/7.jpg';
import app13 from '../../images/landing/8.jpg';
import app14 from '../../images/landing/9.jpg';
import app15 from '../../images/landing/10.jpg';


const images = [
   app1,app2,app3,app4,app5,app6,app7,app8,app9,app10,app11,app12, app13,app14,app15
];
 
const zoomOutProperties = {
  duration: 5000,
  transitionDuration: 500,
  infinite: true,
  indicators: true,
  scale: 0.4,
  arrows: true
}
 
const Slideshow = () => {
    return (
      <div className="slide-container">
        <Zoom {...zoomOutProperties}>
          {
            images.map((each, index) => <img key={index} src={each} />)
          }
        </Zoom>
      </div>
    )
}

