import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import * as pathUrls from './components/constants/pageUrls';
import Routes from './routes';
import loaderIcon from './images/logo/logo1.png'
import $ from 'jquery'; 


class DefaultComponent extends Component {

    componentDidMount = () => {
        // window.addEventListener("beforeunload",function(e){
        //     document.body.className = "preloader";
        //   },false);

        //   /* 1. Proloder */
           
          
    }

    render() {
        let { history } = this.props;
        let isMobile = window.screen.width <= 480;
        let isHomeUrl = history?.location?.pathname===pathUrls.HOME;
        let headerStyle = isHomeUrl ? 'hide' : '';
        let backgroundStyle = isMobile ? 'mobile' : 'desktop';
        $(window).on('load', function () {
            $('#preloader-active').delay(450).fadeOut('slow');
            $('body').delay(450).css({
            'overflow': 'visible'
            });
        });
        return (
            <div id={'main'} className={`main ${backgroundStyle}`}>
                <div className={`route-container ${headerStyle}`}>
                <div id="preloader-active">
                        <div className="preloader d-flex align-items-center justify-content-center">
                            <div className="preloader-inner position-relative">
                                <div className="preloader-circle"/>
                                <div className="preloader-img pere-text">
                                    <img src={loaderIcon} alt=""/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Routes/>
                </div>
            </div>
        );
    }
}

export default connect()(withRouter(DefaultComponent));
